.banner{
    background-size: cover;
    height: 448px;
    color: white;
    margin-top: 69px;
    position: relative;
}

.content{
    padding-top: 11.5%;
    height: 190px; 
}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
    margin-left: 20px;

    
}

.btn{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,0.5);
    cursor: pointer;
    margin-right: 1rem;
    margin-left: 20px;

}

.btn:hover{
    color: black;
    background-color: #e6e6e6;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
    margin-left: 20px;

}

.fade-shade{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
    width: 100%;
    position: absolute;
    bottom: 0;

}
